.row {
    color: white;
    margin-left: 20px;
}
.row__posters{
    display: flex;
    overflow-y: hidden;
    overflow-x: scroll;
    padding: 30px;
}

.row__posters::-webkit-scrollbar{
    display: none;
}

.row__poster {
    object-fit: contain;
    width: 100%;
    max-height: 125px;
    margin-right: 15px;
    transition: transform 450ms;
}

.row__poster:hover {
    transform: scale(1.10);
}

.row__posterLarge {
    max-height: 250px;
}

.row__posterLarge:hover {
    transform: scale(1.15);
}